
body {
	max-height: 100vh;
	background-color: black;
	margin: 0;
}

.main {
	height: 100vh;
	position: absolute;
	z-index: 5;
}

.header {
}
.header__list {
	width: 100vw;
	display: flex;
	justify-content: space-around;
	padding-left: 0;
	
	@media (max-width: 700px) {
		margin-top: 70px;
	}
}
.header__item {
	list-style: none;
}
.header__text {
  font-size: 24px;
  font-family: "Kanalisirung";
  color: rgb(255, 255, 255);
  line-height: 0.5;
  text-align: center;
}
.header__icon {
}
.container {
	display: flex;
	flex-direction: column;
	align-items: center;
}
.header__date {
	font-size: 40px;
  font-family: "Monument Grotesk";
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: rgb(255, 255, 255);
  line-height: 0.1;
  text-align: center;

}
.header__title {
  font-size: 48px;
  font-family: "MARSNEVENEKSK";
  color: rgb(255, 255, 255);
  line-height: 0.25;
  text-align: center;
}
.header__subtitle {
	font-size: 60px;
  font-family: "Monument Grotesk";
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: rgb(255, 255, 255);
  line-height: 1;
  text-align: center;
}
.image__container--s {
	z-index: 150;
}
.image__container {
	width: 230px;
	height: 140px;
	position: relative;
	top: 150px;
	margin-right: auto;
	margin-left: auto;
	background-image: url("../img/modal.png");
	background-repeat: no-repeat;
}
.image__span {
	padding-top: 15px;
  font-size: 18px;
  font-family: "Kanalisirung";
  color: rgb(0, 0, 0);
  line-height: 0.5;
  text-align: center;
}
.image__title {
	margin-top: 0;
	padding-top: 30px;
	padding-left: 25px;
  font-size: 24px;
  font-family: "Kanalisirung";
  color: rgb(0, 0, 0);
  line-height: 0.5;
  text-align: center;
}
.button__container {
	width: 90px;
	height: 30px;
	margin-left: auto;
	margin-right: auto;
	border: 2px solid black;
	text-align: center;
	box-shadow: -3px 0px 1px -1px rgba(0,0,0,.59), 0px 1px 1px 1px rgba(255, 255, 255, 0.5) inset;
}
.button {
	width: 94px;
	text-decoration: none;
  vertical-align: middle;
  font-size: 20px;
  font-family: "Kanalisirung";
  color: rgb(0, 0, 0);
  line-height: 0.5;
  text-align: center;
}

.button__container:hover {
	box-shadow: -1px -1px 0px 2px rgba(255, 255, 255, 0.5) inset;
}


.audio {
	position: relative;
	display: block;
	margin: 0 auto;
	opacity: .6;
	z-index: 150;

	@media (max-width: 700px) {
		display: block;
		opacity: .6;
		z-index: 150;
	}
}

.crew {
	display: block;
	position: relative;
	text-align: center;
	top: 25vh;
	margin: 0 auto;
}