@font-face {
  font-family: 'Monument Grotesk';
  src: local("Monument Grotesk Bold"), local("Monument-Grotesk-Bold"), url("../fonts/MonumentGrotesk-Bold.woff2") format("woff2"), url("../fonts/MonumentGrotesk-Bold.woff") format("woff"), url("../fonts/MonumentGrotesk-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal; }

/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0; }
  .slick-list:focus {
    outline: none; }
  .slick-list.dragging {
    cursor: pointer;
    cursor: hand; }

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0); }

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto; }
  .slick-track:before, .slick-track:after {
    content: "";
    display: table; }
  .slick-track:after {
    clear: both; }
  .slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none; }
  [dir="rtl"] .slick-slide {
    float: right; }
  .slick-slide img {
    display: block; }
  .slick-slide.slick-loading img {
    display: none; }
  .slick-slide.dragging img {
    pointer-events: none; }
  .slick-initialized .slick-slide {
    display: block; }
  .slick-loading .slick-slide {
    visibility: hidden; }
  .slick-vertical .slick-slide {
    display: block;
    height: auto;
    border: 1px solid transparent; }

.slick-arrow.slick-hidden {
  display: none; }

body {
  max-height: 100vh;
  background-color: black;
  margin: 0; }

.main {
  height: 100vh;
  position: absolute;
  z-index: 5; }

.header__list {
  width: 100vw;
  display: flex;
  justify-content: space-around;
  padding-left: 0; }
  @media (max-width: 700px) {
    .header__list {
      margin-top: 70px; } }

.header__item {
  list-style: none; }

.header__text {
  font-size: 24px;
  font-family: "Kanalisirung";
  color: white;
  line-height: 0.5;
  text-align: center; }

.container {
  display: flex;
  flex-direction: column;
  align-items: center; }

.header__date {
  font-size: 40px;
  font-family: "Monument Grotesk";
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: white;
  line-height: 0.1;
  text-align: center; }

.header__title {
  font-size: 48px;
  font-family: "MARSNEVENEKSK";
  color: white;
  line-height: 0.25;
  text-align: center; }

.header__subtitle {
  font-size: 60px;
  font-family: "Monument Grotesk";
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 15px;
  color: white;
  line-height: 1;
  text-align: center; }

.image__container--s {
  z-index: 150; }

.image__container {
  width: 230px;
  height: 140px;
  position: relative;
  top: 150px;
  margin-right: auto;
  margin-left: auto;
  background-image: url("../img/modal.png");
  background-repeat: no-repeat; }

.image__span {
  padding-top: 15px;
  font-size: 18px;
  font-family: "Kanalisirung";
  color: black;
  line-height: 0.5;
  text-align: center; }

.image__title {
  margin-top: 0;
  padding-top: 30px;
  padding-left: 25px;
  font-size: 24px;
  font-family: "Kanalisirung";
  color: black;
  line-height: 0.5;
  text-align: center; }

.button__container {
  width: 90px;
  height: 30px;
  margin-left: auto;
  margin-right: auto;
  border: 2px solid black;
  text-align: center;
  box-shadow: -3px 0px 1px -1px rgba(0, 0, 0, 0.59), 0px 1px 1px 1px rgba(255, 255, 255, 0.5) inset; }

.button {
  width: 94px;
  text-decoration: none;
  vertical-align: middle;
  font-size: 20px;
  font-family: "Kanalisirung";
  color: black;
  line-height: 0.5;
  text-align: center; }

.button__container:hover {
  box-shadow: -1px -1px 0px 2px rgba(255, 255, 255, 0.5) inset; }

.audio {
  position: relative;
  display: block;
  margin: 0 auto;
  opacity: .6;
  z-index: 150; }
  @media (max-width: 700px) {
    .audio {
      display: block;
      opacity: .6;
      z-index: 150; } }

.crew {
  display: block;
  position: relative;
  text-align: center;
  top: 25vh;
  margin: 0 auto; }
